<template>
	<div style="min-width: min-width: 1540px;;">
		<company-header :ticket='ticket' :ccId="ccId" :companyLogo="img" :env="env" @signOut='signOut'>
			<template v-slot:header-content>
				<div class="header-content_box">
					<div class="flex-align-center _box_one cursor-pointer  bule"
						:class="typemold =='home' ? '_box_color':'' " @click="handType('home')">
						<div>
							<Icon type="md-home" style="font-size: 18px;margin-right: 5px;" />
						</div>
						<div>主页</div>
					</div>
					<div class="_box_tow flex-align-center cursor-pointer bule"
						:class="typemold =='Sidebar' ? '_box_color':''" @click="handType('Sidebar')">
						<div>
							<Icon type="md-settings" style="font-size: 18px;margin-right: 5px;" />
						</div>
						<div>系统设置</div>
					</div>
				</div>
			</template>
		</company-header>
	</div>
</template>

<script>
	import img from '@/assets/homelogo.png';
	export default {
		props: {
			typemold: {
				type: String,
				default: () => ''
			}
		},
		data() {
			return {
				userInfo: localStorage.getItem('userInfo') ? JSON.parse(localStorage.getItem('userInfo')) : '', // 借款人单位
				ticket: this.$cookies.get('ticket'),
				ccId: this.$cookies.get('ccId'),
				env: process.env.NODE_ENV == 'production' ? 'production' : 'development',
				img: img
			};
		},
		watch: {
			type(newValue, oldValue) {
				console.log(newValue)
				console.log(oldValue)
			}
		},
		mounted() {
			console.log(process.env.NODE_ENV);
			// this.env = process.env.NODE_ENV == 'development' ? 'production' : 'development',
			console.log('环境',this.env);
			
		},
		methods: {
			handType(row) {
				let type = row
				localStorage.setItem('headTopType', type)
				this.$emit('handType', type);
				if (row == 'home') {
					this.$router.push({
						path: '/Home'
					})
				}
				if (row == 'Sidebar') {
					this.$router.push({
						path: '/EnterpriseInfo'
					})
				}
			},


			signOut() {
				console.log(123)
			},


		}
	}
</script>


<style lang="less" scoped>
	.header-content_box {
		height: 100%;
		display: flex;
		align-items: center;
		font-size: 14px;

		// margin-top: 20px;
		._box_one {
			height: 100%;
			padding: 25px;
			margin-left: 50px;
		}

		._box_tow {
			height: 100%;
			padding: 25px;
		}

		._box_color {
			background: #f9f9f9;
			color: #2d8cf0;
			border-bottom: 2px solid #2d8cf0;
		}
	}
</style>
<style>
	/* .header_wrap /deep/  */
	.header_wrap .header-content {
		display: flex;
		align-items: center;
	}
</style>
