// import * as dayjs from 'dayjs'
import dayjs from "dayjs"
export default {
	// 获取Cooke
	getCookie(cookie_name) {
		var allcookies = document.cookie;
		var cookie_pos = allcookies.indexOf(cookie_name); //索引的长度

		// 如果找到了索引，就代表cookie存在，
		// 反之，就说明不存在。
		if (cookie_pos != -1) {
			// 把cookie_pos放在值的开始，只要给值加1即可。
			cookie_pos += cookie_name.length + 1; //这里容易出问题，所以请大家参考的时候自己好好研究一下
			var cookie_end = allcookies.indexOf(";", cookie_pos);

			if (cookie_end == -1) {
				cookie_end = allcookies.length;
			}

			var value = unescape(allcookies.substring(cookie_pos, cookie_end)); //这里就可以得到你想要的cookie的值了。。。
		}
		return value;
	},
	/* 合并请求头 */
	combineHeader(headerConfig) {
		return function(header, opt = {}) {
			let {
				Appkey,
				AppSecret
			} = headerConfig;
			let timeTamp = parseInt(new Date().getTime() / 1000);
			let LcSign = md5(Appkey + timeTamp + AppSecret);
			let userInfo = localStorage.getItem('userInfo') ? JSON.parse(localStorage.getItem('userInfo')) : {};
			let session_id = userInfo.sessionId || '' // 企业微信session_id
			if (header.setRequestHeader) {
				header.setRequestHeader('LC-Appkey', Appkey);
				header.setRequestHeader('LC-Sign', LcSign);
				header.setRequestHeader('LC-Timestamp', timeTamp);
				header.setRequestHeader('LC-Session', session_id);
				for (let key in opt) {
					header.setRequestHeader(key, opt[key]);
				}
				return;
			}
			header['LC-Appkey'] = Appkey;
			header['LC-Sign'] = LcSign;
			header['LC-Timestamp'] = timeTamp;
			header['LC-Session'] = session_id;
			for (let key in opt) {
				header[key] = opt[key];
			}
		}
	},
	// window.combineHeader = combineHeader(headerConfig)

	// 循环处理时间
	checkShowRule(arr, key) {
		var newArr = arr.map((item, index, array) => {
			var obj = this.toggleTime(item[key]);
			item['show_time_type'] = obj.type;
			item['show_time'] = obj.time;
			if (index > 0) {
				item['is_show_time'] = this.compareTimeInterval(array[index - 1][key], array[index][key]);
			} else {
				item['is_show_time'] = true;
			}
			return item;
		});
		return newArr;
	},
	toggleTime(date) {
		var time;
		var type = this.getDateDiff(date);
		//1：新消息，2：当天消息,3：昨天消息，4：今年消息，5：其他消息
		if (type == 1) {
			time = "以下为最新消息"; //新消息，不显示时间，但是要显示"以下为最新消息"
		} else if (type == 2) {
			time = dayjs(date).format("H:mm"); //当天消息，显示：10:22
		} else if (type == 3) {
			time = dayjs(date).format("昨天 H:mm"); //昨天消息，显示：昨天 20:41
		} else if (type == 4) {
			time = dayjs(date).format("M月D日 AH:mm").replace("AM", "上午").replace("PM",
			"下午"); //今年消息，上午下午，显示：3月17日 下午16:45
		} else if (type == 5) {
			time = dayjs(date).format("YYYY年M月D日 AH:mm").replace("AM", "上午").replace("PM",
				"下午"); //其他消息，上午下午，显示：2020年11月2日 下午15:17
		}
		return {
			time: time,
			type: type
		};
	},
	getDateDiff(date) {
		var nowDate = dayjs(new Date()); //当前时间
		var oldDate = dayjs(new Date(date)); //参数时间
		var result;
		if (nowDate.year() - oldDate.year() >= 1) {
			result = 5;
		} else if (nowDate.month() - oldDate.month() >= 1 || nowDate.date() - oldDate.date() >= 2) {
			result = 4;
		} else if (nowDate.date() - oldDate.date() >= 1) {
			result = 3;
		} else if (nowDate.hour() - oldDate.hour() >= 1 || nowDate.minute() - oldDate.minute() >= 5) {
			result = 2;
		} else {
			result = 1;
		}
		return result;
	},
	compareTimeInterval(t1, t2) {
		// console.log(t1,t2,dayjs(t2)-dayjs(t1));
		return dayjs(t2) - dayjs(t1) >= 300000 ? true : false;
	},
	
	
	getDateFun (d) {
		console.log(123);
	  let obj = null;
	  if (d) {
	    let time = d;
	    if (!this.verification(time, 'Number')) {
	      time = time.replace(/\-/g, "/");
	    }
	    this.date = new Date(time);
	  } else {
	    this.date = new Date();
	  }
	  let year = this.date.getFullYear().toString();
	  let month = (this.date.getMonth() + 1).toString();
	  let day = this.date.getDate().toString();
	  let dayCount = this.date.getDate();
	  let whatDay = this.date.getDay();
	  let time = Math.ceil(this.date.getTime() / 1000);
	  let h = this.date.getHours() < 10 ? '0' + this.date.getHours() : this.date.getHours().toString();
	  let f = this.date.getMinutes() < 10 ? '0' + this.date.getMinutes() : this.date.getMinutes().toString();
	  let m = this.date.getSeconds() < 10 ? '0' + this.date.getSeconds() : this.date.getSeconds().toString();
	  obj = {
	    year: year,
	    month: (month < 10 ? '0' + month : month),
	    time: time,
	    day: (day < 10 ? '0' + day : day),
	    h: h,
	    f: f,
	    m: m,
	    whatDay: whatDay,
	    dayCount: dayCount,
	    date2: `${year + '年' + (month < 10 ? '0' + month : month) + '月' + (day < 10 ? '0' + day : day)}日`,
	    date: `${year + '-' + (month < 10 ? '0' + month : month) + '-' + (day < 10 ? '0' + day : day)}`,
	    date1: `${year + '/' + (month < 10 ? '0' + month : month) + '/' + (day < 10 ? '0' + day : day)}`,
	    datetime: `${year + '-' + (month < 10 ? '0' + month : month) + '-' + (day < 10 ? '0' + day : day) + ' ' + h + ':' + f + ':' + m}`
	  };
	  return obj;
	},
	verification (data, type) {
	  let gettype = Object.prototype.toString;
	  // 字符串
	  if (gettype.call(data) === '[object String]' && type === 'String') {
	    return true;
	  }
	  // 整数
	  if (gettype.call(data) === '[object Number]' && type === 'Number') {
	    return true;
	  }
	  // 布尔值
	  if (gettype.call(data) === '[object Boolean]' && type === 'Boolean') {
	    return true;
	  }
	  //  Undefined
	  if (gettype.call(data) === '[object Undefined]' && type === 'Undefined') {
	    return true;
	  }
	  // 空
	  if (gettype.call(data) === '[object Null]' && type === 'Null') {
	    return true;
	  }
	  // 对象
	  if (gettype.call(data) === '[object Object]' && type === 'Object') {
	    return true;
	  }
	  // 数组
	  if (gettype.call(data) === '[object Array]' && type === 'Array') {
	    return true;
	  }
	  // 函数
	  if (gettype.call(data) === '[object Function]' && type === 'Function') {
	    return true;
	  }
	  return false;
	}
}
