<template>
	<div id="app">
		<Layout>
			<Header :style="{width: '100%'}">
				<ApprovalExplain :typemold="headTopType" @handType="handType"></ApprovalExplain>
			</Header>
			<router-view />
		</Layout>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				list: [],
				headTopType: localStorage.getItem('headTopType'),
				activeName: this.$route.path,
			};
		},
		watch: {
			'$route'(to) {
				this.$nextTick(() => {
					this.activeName = to.path
					this.headTopType = localStorage.getItem('headTopType')
				})
			}
		},
		mounted() {},
		methods: {
			handType(row) {
				localStorage.setItem('headTopType', row)
			},
		},
	};
</script>
<style scoped>
	.ivu-layout {
		height: 100%;
		/* overflow-y: hidden; */
	}

	.ivu-card-body {
		height: 100%;
	}

	.ivu-layout.ivu-layout-has-sider>.ivu-layout,
	.ivu-layout.ivu-layout-has-sider>.ivu-layout-content {
		overflow-x: auto;

	}

	.ivu-layout-header {
		padding: 0;
		line-height: 1.5;
		font-size: 14px;
		height: 60px;
		min-width: 1540px;
	}

	.contentItem /deep/ .ivu-card-body {
		padding: 0px;
	}

	.ivu-menu-vertical.ivu-menu-light:after {
		width: 0px;
	}

	.ivu-menu-light {
		text-align: center;
	}

	/* 	.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu):after{
		width: 0px;
	} */
</style>
<style lang="less">
	@import './less/reset.less';
	@import './less/app.less';

	html,
	body,
	#app {
		height: 100%;
		width: 100%;
		background: #F9F9F9;
		// overflow-y: hidden;
	}

	// 全局定义表格内的滚动条样式
	.ivu-table-overflowY::-webkit-scrollbar {
		/*高宽分别对应横竖滚动条的尺寸*/
		width: 5px;
		height: 5px;
	}

	.ivu-table-overflowY::-webkit-scrollbar-thumb {
		/*滚动条里面小方块*/
		background-color: #f0f0f0;
		// background-color:#fff;  // 
		-webkit-border-radius: 2em;
		-moz-border-radius: 2em;
		border-radius: 2em;
	}

	.ivu-table-overflowY::-webkit-scrollbar-thumb:hover {
		/* 鼠标移入滑块 */
		background-color: rgba(0, 0, 0, .3);
	}

	.ivu-table-overflowY::-webkit-scrollbar-track {
		/*滚动条里面轨道*/
		background-color: #fff;
		-webkit-border-radius: 2em;
		-moz-border-radius: 2em;
		border-radius: 2em;
	}
</style>
