import Vue from "vue";
import VueRouter from "vue-router";
// import Home from "../views/Home.vue";



const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
}
Vue.use(VueRouter);

const routes = [{
		path: "/",
		redirect: '/Home'
	},
	// 404
	{
		path: '*',
		component: () => import('@/views//err404.vue')
	},
	// 未开通
	{
		path: '/opening',
		component: () => import('@/views//opening.vue')
	},
	// 主页
	{
		path: '/',
		component: () => import('@/layouts/Navbar.vue'),
		children: [{
			path: '/Home',
			component: () => import('@/views/Home.vue')
		}]
	},

	// 有导航菜单的
	{
		path: '/',
		component: () => import('@/layouts/NavbarSidebar.vue'),
		children: [{
				path: '/BasicSetuUp',
				component: () => import('@/views/BasicSetuUp.vue')
			},
			{
				path: '/CustomerCapital',
				component: () => import('@/views/CustomerCapital.vue')
			},
			{
				path: '/FansManagement',
				component: () => import('@/views/FansManagement.vue')
			},
			{
				path: '/DialogSettings',
				component: () => import('@/views/DialogSettings.vue')
			},
			{
				path: '/DataAnalysis',
				component: () => import('@/views/DataAnalysis.vue')
			},
			{
				path: '/EnterpriseInfo',
				component: () => import('@/views/EnterpriseInfo.vue')
			},
			{
				path: '/StaffManagement',
				component: () => import('@/views/StaffManagement.vue')
			},

		]
	},

];

const router = new VueRouter({
	mode: "history",
	base: process.env.BASE_URL,
	routes,
});

export default router;
