import Axios from 'axios'
import qs from 'qs'
import router from "@/router";
// import { combineHeader } from '../utils/util.js'
// import Vue from 'vue';
import {
	Message
} from 'view-design';
import cookies from "vue-cookies";
// import store from '../store/index.js';


// console.log('URL',process.env.VUE_APP_BASE_API)
Axios.defaults.timeout = 10000; // 响应时间
Axios.defaults.baseURL = process.env.VUE_APP_BASE_API;
Axios.defaults.withCredentials = false;

Axios.interceptors.request.use(config => {
	if (config.contentType === 'formData') {
		config.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
		config.data = qs.stringify(config.data) // 转为formdata数据格式
	} else {
		config.headers['Content-Type'] = 'application/json;charset=UTF-8';
	}
	if (process.env.NODE_ENV === "development") {
		// console.log('我是测试环境我将写死ticket');
		cookies.set('ticket', '6kDg0lcCOj4DK4a54CT5Cp4AEDKkhGC4E1QrpNIRUYbYFF51impC54ipMhKT3WJ2') //写死ticket
	}

	let ticket = cookies.get('ticket')
	let companyId = cookies.get('companyId')
	config.headers['ticket'] = ticket
	config.headers['companyId'] = companyId
	return config;
}, error => {
	return Promise.reject(error);
});


Axios.interceptors.response.use(response => {
	return response;
}, error => {
	return Promise.reject(error);
});

/**
 * 公共请求入口
 * @param param 'object'
 * @param url   'string'
 * @param type  'get || post'
 * @param contentType 'formData || json'
 * @returns {Promise}
 */

export const commonPromise = (param = {}, url, type, contentType) => {
	// store.commit('isLoading',true)
	return new Promise((resolve, reject) => {
		// let userInfo = localStorage.getItem('userInfo') ? JSON.parse(localStorage.getItem('userInfo')) : {}
		// param['USERID'] = userInfo.admin_id || ''
		// let request = type && type.toLowerCase() === 'post' ? Axios({ url, method: 'post', data: param, contentType }) : Axios({ url, method: 'get', params: param })


		let request = null
		let methodtype = type && type.toLowerCase()
		// 那个逻辑页面呢
		if (methodtype === 'post') {
			request = Axios({
				url,
				method: 'post',
				data: param,
				contentType
			})
		} else if (methodtype === 'blob') {
			request = Axios({
				url,
				responseType: 'blob',
				method: 'post',
				data: param,
				contentType
			})
		} else {
			request = Axios({
				url,
				method: 'get',
				params: param
			})
		}

		request.then(res => {
			if (methodtype === 'blob') {
				resolve(res)
			} else if (res.status === 200) {
				// 成功
				if (res.data.code === 1000) {
					resolve(res.data)
				} else if (res.data.code === 1004) {
					reject(new Error('用户状态变更'));
					localStorage.removeItem('userInfo');
					res.data.msg && Message.info(res.data.msg);
					router.push('/');
				} else if (res.data.code == 6001) {
					reject(res.data)
					Message.info(`${res.data.msg}，3秒后返回首页`);
					setTimeout(() => {
						window.location.href = process.env.NODE_ENV == "production" ? 'https://i.lyfz.net/':'https://i.27yn.cn/'
					}, 3000);
				} else if (res.data.code == 6002) {
					Message.info(res.data.msg);
					reject(res.data)
					router.push('/opening');
				} 
				else {
					reject(res.data)
					Message.info(res.data.msg);
				}
			} else if (res.status === 403) { // 用户状态变更
				localStorage.removeItem('userInfo');
				res.data.msg && Message.info(res.data.msg);
				router.push('/');
				reject(new Error('用户状态变更'));
			} else {
				res.data.msg && Message.info(res.data.msg);
				reject(new Error('系统出错'));
			}
		}).catch(err => {
			if (err.toString().includes('403')) {
				localStorage.removeItem('userInfo');
				router.push('/');
			}
		});
	})
}
