<template>
	<div>
		<Modal :closable='false' width="1000" v-model="modal10" :footer-hide='true' 
		 :styles="{top: '70px'}"
		>
		<!-- class-name="vertical-center-modal" -->
			<div class="chatBox">
				<div class="chatBox_lift">
					<div class="chatBox_lift_input">
						<Input placeholder="搜索昵称" v-model="search" @on-focus="handFocus" @on-blur="handBlur"
							@on-change="handSearchFans()" style="width: 280px">
						<Icon type="ios-search" slot="suffix" />
						</Input>
					</div>

					<div v-if="!searchType" class="chatBox_lift_list">
						<div v-for="(item,index) in recordList" :key="index" @click.stop="handOneFanChat(item)"
							:class="item.wxId==wxId?'choose':'' " class="chatBox_lift_item cursor-pointer">
							<div class="_lift_item_img">
								<img :src="item.portrait" width="36" height="36" />
							</div>
							<div class="_lift_item_text">
								<div class="item_text_top" :class="item.wxId==wxId?'color_FFF':'' ">{{item.nickname}}</div>
								<div class="item_text_bot word-overstep-hide" :class="item.wxId==wxId?'color_FFF':'' ">{{item.content}}</div>
							</div>
							<div class="_lift_item_icon" @click.stop="handDelRecord(item)">
								<Icon type="ios-close cursor-pointer" size="14px" />
							</div>
						</div>
					</div>

					<div v-else class="chatBox_lift_list">
						<!-- <div class="searchType_top">联系人</div> -->
						<div v-for="(item,index) in searchLIst" :key="index" @click="handSearchOneFanChat(item)"
							class="chatBox_lift_item cursor-pointer">
							<div class="_lift_item_img">
								<img :src="item.portrait" width="36" height="36" />
							</div>
							<div class="_lift_item_text">
								<div class="item_text_top">{{item.nickname}}</div>
							</div>
						</div>
					</div>

				</div>
				<div class="chatBox_right">
					<div class="chatBox_right_top">
						<div class="chatBox_right_top_name">{{nickname}}</div>
						<div @click="handmodalfalse">
							<Icon type="ios-close cursor-pointer" size="32" />
						</div>
					</div>
					<!-- 这个是成功的例子 -->
					<div class="chatBox_right_in" id="gundong">
						<GeminiScrollbar class="my-scroll-bar"  :autoshow="true">
							<div v-for="(item,index) in listTime" :key="index" class="chatBox_right_item">
								<div class="item_show_time" v-if="item.is_show_time==true"> {{item.show_time}}</div>
								<div :class="(item.messageDirection == 1 || item.messageDirection == 2) ? '_item_right':'_item_lift'">
									{{item.content}}
									<div v-if="item.messageDirection !== 3 && item.isAbnormal== 1" class="icon_isAbnormal">
										<Icon type="ios-alert" size="24" />
									</div>
								</div>
								
							</div>
						</GeminiScrollbar>
					</div>


					<div class="chatBox_right_bou">
						<Input v-model="value19" type="textarea" :rows="5" :border="false" placeholder="请输入内容..."
							@on-enter="handSendMsg" />
					</div>
					<div class="chatBox_right_but">
						<Button type="primary" @click="handSendMsg">发送</Button>
					</div>
				</div>
			</div>
		</Modal>
	</div>
</template>

<script>
	import {
		indexApiModules
	} from '@/api';
	import util from '../../utils/util.js';
	export default {
		// props: {
		// 	// list: {
		// 	// 	type: Object,
		// 	// 	default: () => {}
		// 	// }
		// },
		data() {
			return {
				modal10: false,
				wxAppId: localStorage.getItem('wxAppId'),
				search: '',
				searchType: false,
				searchLIst: [],
				value19: '',
				wxId: '',
				nickname: '',
				recordList: [],
				list1: [],
				listTime: [],
			};
		},
		mounted() {

		},
		methods: {

			handgetRecord() {
				indexApiModules.getRecord({
					wxAppId: this.wxAppId,
					wxId: this.wxId,
				}).then(res => {
					this.recordList = res.data
				})
			},
			handgetGetOneFanChat() {
				indexApiModules.getOneFanChat({
					wxId: this.wxId,
				}).then(res => {
					this.list1 = res.data
					this.listTime = util.checkShowRule(this.list1, 'creationTime')
					this.$nextTick(() => {
						let msg = document.getElementById('gundong') // 获取对象
						msg.scrollTop = msg.scrollHeight // 滚动高度
					})

				})
			},


			handOneFanChat(row) {
				this.wxId = row.wxId
				this.nickname = row.nickname
				this.handgetGetOneFanChat()
			},
			handSendMsg() {
				if (this.value19 == '') {
					this.$Message.success('不能发送空消息');
					return
				}
				indexApiModules.sendMsg({
					wxId: this.wxId,
					content: this.value19
				}).then(res => {
					console.log('12', res)
					this.value19 = ''
					this.handgetGetOneFanChat()

				})
			},
			// 删除用户
			handDelRecord(row) {
				console.log('1312', row.chatRecordId)
				indexApiModules.delRecord({
					chatRecordId: row.chatRecordId
				}).then(res => {
					console.log('12', res)
					if (row.wxId == this.wxId) {
						this.wxId = ""
						this.handgetRecord()
						this.wxId = this.recordList[0].wxId
						setTimeout(() => {
							this.handgetGetOneFanChat()
						}, 1000);
					} else {
						this.handgetRecord()
					}

				})

			},

			handmodalfalse() {
				this.modal10 = false
				this.value19 = ''
			},
			modalTrue(row) {
				console.log(row)
				this.wxId = row.wxId
				this.nickname = row.nickname
				this.modal10 = true
				this.handgetRecord()
				this.handgetGetOneFanChat()
			},
			handSearchFans() {
				indexApiModules.searchFans({
					wxAppId: this.wxAppId,
					search: this.search
				}).then(res => {
					console.log('12', res)
					this.searchLIst = res.data
				})
			},
			// 点击后把 选中的用户放第一个
			handSearchOneFanChat(row) {
				console.log('123123', row)
				this.wxId = row.wxId
				this.nickname = row.nickname
				this.handgetRecord()
				setTimeout(() => {
					this.handgetGetOneFanChat()
				}, 1000);


			},

			// 获得焦点
			handFocus() {
				this.$nextTick(() => {
					this.searchType = true
				})

			},
			// 失去焦点
			handBlur() {
				this.$nextTick(() => {
					setTimeout(() => {
						this.searchType = false
						this.search = ''
						this.searchLIst = []
					}, 500);
				})
			},

		}
	}
</script>

<style lang="less" scoped>
	.chatBox {
		height: 600px;

		.chatBox_lift {
			float: left;
			width: 300px;
			height: 100%;


			.chatBox_lift_input {
				text-align: center;
				border-bottom: 1px solid #DDDDDD;
				padding: 10px 15px;
			}

			.chatBox_lift_list {
				height: 545px;
				// overflow-y: scroll;
				overflow-y: hidden;
				.searchType_top {
					padding: 10px 0;
					// border-bottom: 1px solid #DDDDDD;
					// background: #F3F3F3;
					text-align: center;
				}

				.chatBox_lift_item {
					position: relative;
					display: flex;
					align-items: center;
					padding: 10px 15px;
					// border-bottom: 1px solid #DDDDDD;

					._lift_item_img {
						margin-right: 10px;
					}

					._lift_item_text {
						.item_text_top {
							font-size: 14px;
							font-weight: 600;
							color: #333333;
							margin-bottom: 5px;
							// color: inherit;
						}

						.item_text_bot {
							width: 200px;
							font-size: 12px;
							font-weight: 400;
							color: #666666;
							// color: inherit;
						}
					}

					._lift_item_icon {
						position: absolute;
						// right: 8px;
						left: 275px;
						top: 8px;
					}
				}

				.chatBox_lift_item:hover {
					background: #E4E7EB;
					color: #FFFFFF !important;
				}

				.choose {
					background: #2d8cf0;
					color: #FFFFFF !important;
				}
			}
			.chatBox_lift_list:hover{
				overflow-y: scroll;
			}

			// 自定义滚动条样式 已经有轮廓了我给你一个
			// 自定义滚动条
			.chatBox_lift_list::-webkit-scrollbar {
				/*高宽分别对应横竖滚动条的尺寸*/
				width: 5px;
				height: 5px;
			}

			.chatBox_lift_list::-webkit-scrollbar-thumb {
				/*滚动条里面小方块*/
				// background-color: #f0f0f0;
				background-color: rgba(0,0,0,.3);
				
				// background-color:#fff;  // 
				-webkit-border-radius: 2em;
				-moz-border-radius: 2em;
				border-radius: 2em;
			}

			.chatBox_lift_list::-webkit-scrollbar-thumb:hover {
				/* 鼠标移入滑块 */
				background-color: rgba(0, 0, 0, .3);
			}

			.chatBox_lift_list::-webkit-scrollbar-track {
				/*滚动条里面轨道*/
				background-color: #f0f0f0;
				-webkit-border-radius: 2em;
				-moz-border-radius: 2em;
				border-radius: 2em;
			}
		}


		.chatBox_right {
			position: relative;
			height: 100%;
			overflow: hidden;
			border-left: 1px solid #DDDDDD;

			.chatBox_right_top {
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding: 10px 15px;
				border-bottom: 1px solid #DDDDDD;

				.chatBox_right_top_name {
					font-size: 24px;
					font-weight: 600;
					color: #333333;
					line-height: 32px;
				}
			}


			.chatBox_right_in {
				height: 430px;
				padding-bottom: 20px;

				.my-scroll-bar {
					height: 430px;
					overflow-y: scroll;

					// overflow:auto
					// overflow-y: scroll;
					.chatBox_right_item {
						overflow: hidden;

						.item_show_time {
							display: flex;
							justify-content: center;
							align-items: center;
							width: 100%;
							padding-top: 20px;
						}

						._item_lift {
							// display: inline-block;
							// text-align: left;
							max-width: 300px;
							word-break: break-all;
							width: fit-content;
							width: -webkit-fit-content;
							width: -moz-fit-content;
							margin-left: 18px;
							margin-top: 20px;
							padding: 7px 15px;
							background: #E4E7EB;
							border-radius: 2px 12px 12px 12px;
						}

						._item_right {
							max-width: 300px;
							word-break: break-all;
							// display: flex;
							// flex-direction: row-reverse;
							// display: inline-block;
							float: right;
							// text-align: right;
							margin-right: 18px;
							margin-top: 20px;
							padding: 7px 15px;
							background: #C6E5FF;
							border-radius: 12px 2px 12px 12px;
							position: relative;
							.icon_isAbnormal{
								 position: absolute;
								 top: 10px;
								 left: -30px;
								 color: red;
							}
						}
					}

					// 现在最后一个元素
					.chatBox_right_item:nth-last-child(1) {
						margin-bottom: 20px;
					}

				}
			}

			.chatBox_right_bou {
				border-top: 1px solid #DDDDDD;
			 /deep/ textarea.ivu-input{
					    resize: none;
				}
			}

			.chatBox_right_but {
				position: absolute;
				right: 25px;
				bottom: 20px;
			}
		}

	}
</style>




<style lang="less">
	.ivu-modal-content {
		// display: flex;
		// align-items: center;
		// justify-content: center;

		.ivu-modal-body {
			padding: 0;
		}

		.ivu-modal {
			top: 0;
		}
	}

	// 自定义滚动条样式 已经有轮廓了我给你一个
	// 自定义滚动条
	.ivu-input::-webkit-scrollbar {
		/*高宽分别对应横竖滚动条的尺寸*/
		width: 5px;
		height: 5px;
	}

	.ivu-input::-webkit-scrollbar-thumb {
		/*滚动条里面小方块*/
		background-color: #f0f0f0;
		// background-color:#fff;  // 
		-webkit-border-radius: 2em;
		-moz-border-radius: 2em;
		border-radius: 2em;
	}

	.ivu-input::-webkit-scrollbar-thumb:hover {
		/* 鼠标移入滑块 */
		background-color: rgba(0, 0, 0, .3);
	}

	.ivu-input::-webkit-scrollbar-track {
		/*滚动条里面轨道*/
		background-color: #fff;
		-webkit-border-radius: 2em;
		-moz-border-radius: 2em;
		border-radius: 2em;
	}


	/* override gemini-scrollbar default styles */

	/* vertical scrollbar track */
	.gm-scrollbar.-vertical {
		background-color: #f0f0f0
	}

	/* horizontal scrollbar track */
	.gm-scrollbar.-horizontal {
		background-color: transparent;
	}

	/* scrollbar thumb */
	.gm-scrollbar .thumb {
		// background-color: rebeccapurple;
		background-color: red
	}

	.gm-scrollbar .thumb:hover {
		background-color: fuchsia;
	}
</style>
